<template>
  <div style="margin: 30px">
    <el-divider></el-divider>
    <el-descriptions
      class="margin-top"
      title="模型实验信息"
      style="margin-top: 25px"
      :column="3"
      :size="size">
      <template slot="extra">
        <router-link
          :to="{ name: '模型实验', params: {experiment_id: 
               this.$route.params.experiment_id}}"
          style="text-decoration: none; color: aliceblue"
          align="right">
          <el-button>
              <i class="el-icon-arrow-left" style="margin-right: 10px"></i>返回模型实验</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="开始时间">
          {{ modelRun.info.start_time | formatDate}}
      </el-descriptions-item>
      <el-descriptions-item label="Lifecycle Stage">
          {{ modelRun.info.lifecycle_stage}}
      </el-descriptions-item>
      <el-descriptions-item label="责任人">
          {{ modelRun.info.user_id }}
      </el-descriptions-item>
      <el-descriptions-item label="实验状态">
          {{ modelRun.info.status }}
      </el-descriptions-item>
    </el-descriptions>
    <el-divider></el-divider>

     <el-collapse v-model="activeNames">
            <el-collapse-item title="描述" name="1">
                <div> 
                    <slot :description="description" class="main-a">
                    <span v-show="!editModelRunDescVisible">{{modelRun.tags[0].value}}</span>
                    <el-input v-model="editModelForm.description" type="textarea" 
                              :autosize="{ minRows: 4, maxRows: 6}"
                              class="modelDescStyle"
                              v-show="editModelRunDescVisible">
                    </el-input>
                    <el-button  v-show="!editModelRunDescVisible" 
                               class="inline" 
                               icon="el-icon-edit" 
                               type="text"  
                               @click="handleModelDescEdit('index',{description:'description'})" 
                               style="padding: 0px; margin-right: 10px;">
                    </el-button>
                    <br>
                     <span v-show="editModelRunDescVisible">
                        <el-button type="text"  @click.native="editModelRunDescVisible = false" icon="el-icon-back">取消</el-button>
                        <el-button type="text" @click.native="editSubmit" :loading="editLoading" icon="el-icon-check">保存</el-button>
                    </span>
                   </slot>
                </div>
              </el-collapse-item>
          <el-collapse-item title="实验列表" name="2">
              <!--列表-->
              <el-table :data="modelRunsList"
                        highlight-current-row
                        v-loading="listLoading"
                        @selection-change="selsModelRunTracking" style="width: 100%;">
                  <el-table-column type="selection" min-width="5%">
                  </el-table-column>
                  <el-table-column prop="info.run_id" label="run_id" min-width="35%">
                      <template slot-scope="scope">
                          <img v-show="scope.row.info.status==='RUNNING'" src="@/assets/icon-yes.svg"/>
                          <img v-show="scope.row.info.status!='RUNNING'" src="@/assets/icon-no.svg"/>
                          &nbsp;
                          <el-icon name="info.run_id"></el-icon>
                                      <router-link :to="{ name: '模型实验详情', params: {
                                                        run_id: scope.row.info.run_id}}"
                                       style='text-decoration: none;'>{{scope.row.info.run_id }}
                          </router-link>
                      </template>
                  </el-table-column>
                  <el-table-column prop="info.name" label="名称" min-width="10%">
                  </el-table-column>
                  <el-table-column  prop="info.start_time" label="开始时间" min-width="20%" sortable show-overflow-tooltip>
                      <template slot-scope="scope">
                        {{scope.row.info.start_time | formatDate}}
                      </template>
                  </el-table-column>
                  <el-table-column prop="info.user_id" label="责任人" min-width="10%">
                  </el-table-column>
                  <el-table-column prop="info.status" label="当前状态" min-width="10%">
                  </el-table-column>
              </el-table> <!--工具条-->
              <el-col :span="24" class="toolbar">
                  <el-button type="success" @click="compareVersion" :disabled="this.selsModelRunCompare.length===0">Compare</el-button>
                  <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
                  </el-pagination>
              </el-col>
           </el-collapse-item>
       </el-collapse>
  </div>
</template>

<script>

import { test, getExperimentbaseInfo, getModelRun} from '@/api/api'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'

export default {
    data () {
        return {
            _filters: {
                name: ''
            },
            modelRun:{},
            selsModelRunCompare: [],
            modelRunsList: [],
            editLoading:false,
            ExperimentbaseInfo:{},
            editModelRunDescVisible: false,
            activeNames: ['2'],
            sels:[],
            total: 0,
            page: 1,
            listLoading: false,
            editModelForm:{
                description: ''
            }
        }
    },
  created () {
    // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
    // this.form.Tasks = this.Tasks[0].id
  },
    filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
  methods: {
      // 获取模型版本列表
      _getModelRun () {
          this.listLoading = true
          let self = this
          let params = {
              run_id: this.$route.params.run_id,
              user_key: JSON.parse(sessionStorage.getItem('name')),
          }
          let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          }
          getModelRun(headers, params).then(_data => {
              let { msg, code, data } = _data
              self.listLoading = false
              if (code === '999999') {
                  self.modelRun = data
                  console.log(self.modelRun,"self.modelRun")
              } else {
                  self.$message.error({
                      message: msg,
                      center: true
                  })
              }
          })
      },
      // 显示编辑界面
      handleModelDescEdit: function (index, row) {
          this.editModelRunDescVisible = true
          let desc = this.modelRun.tags[0].value || "无"
          // this.editForm = Object.assign({}, row);
          this.editModelForm = Object.assign({}, { description: desc })
      },
      disabledCheckBox(row, index) {
          if (row.is_dir===false) {
              return true
          } else {
              return false
          }
      },

      handleModelOps(command) {
          console.log(command,"command")
           this.$router.push(
               {
                   name: '模型即服务',
                   params:
                   {
                       project_id: this.$route.params.project_id,
                       task_id: this.$route.params.task_id,
                       model_id: command
                   }
               })
          this.$message('click on item ' + command);
          //"handleModelOps(scope.$index,scope.row)"
      },
    // 获取模型任务列表
    _getExperimentbaseInfo () {
      this.listLoading = true
      let self = this
      let params = {
        experiment_id: this.$route.params.experiment_id,
        user_key: JSON.parse(sessionStorage.getItem('name'))
      }

      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getExperimentbaseInfo(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.listLoading = false
        if (code === '999999') {
            self.ExperimentbaseInfo = data
            //console.log(self.ModelbaseInfo,"ModelbaseInfo")
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    editSubmit: function(){
        let self = this
        this.editLoading= true
        let param = JSON.stringify({ 
            run_id: this.$route.params.run_id,
            description: this.editModelForm.description,
            user_key: JSON.parse(sessionStorage.getItem('name'))
        })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'post',
        url: test + '/api/experiments/edit_modelrun_description',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            // console.log(data,"dddddd")
            this.editLoading= false
            this.editModelRunDescVisible = false
            this._getModelRun()
            
        }
      })
        
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },

    handleCurrentChange (val) {
      this.page = val
      this._getModelRun()
    },
    selsModelRunTracking: function (sels) {
      this.selsModelRunCompare = sels
    },
      //比较模型版本信息
      compareVersion: function () {
          let ids = this.selsModelRunCompare.map(item => item.id)
          let self = this
          this.$confirm('确认比较选中记录吗？', '提示', {
              type: 'warning'
          }).then(() => {
              self.listLoading = true
              let params = {
                  project_id: Number(this.$route.params.project_id),
                  ids: ids
              }
              let headers = {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              }
              this.$router.push(
                  {
                      name: '模型实验跟踪详情',
                      params:
                      {
                          project_id: this.$route.params.project_id,
                          task_id: this.$route.params.task_id,
                          exp_id: ids[0],
                      },
                      query:{exp_id_list: ids}
                  })
              // eslint-disable-next-line no-undef
          })
      },
  },
  mounted () {
      this._getModelRun()
      this._getExperimentbaseInfo()
  }
}
</script>
